import {
    GET_EVENT,
    GET_EVENTS,
    EVENT_ERROR,
    UPDATE_EVENT,
    CLEAR_EVENT
  } from '../actions/types';
  
  const initialState = {
    event: null,
    events: [],
    loading: true,
    error: {}
  };
  
  export default function (state = initialState, action) {
    const { type, payload } = action;
  
    switch (type) {
      case GET_EVENT:
      case UPDATE_EVENT:
        return {
          ...state,
          event: payload,
          loading: false
        };
      case GET_EVENTS:
        return {
          ...state,
          events: payload,
          loading: false
        };
      case EVENT_ERROR:
        return {
          ...state,
          error: payload,
          loading: false,
          event: null
        };
      case CLEAR_EVENT:
        return {
          ...state,
          event: null,
        };
      default:
        return state;
    }
  }
  